.Footer{
    color:var(--primary-color);
    background-color: var(--card-bg);
    box-shadow: rgba(0, 0, 0, 0.4) 0px 1px 4px;
    border-radius: 5px;
    padding: 20px;
    margin: auto;
    margin-top: 100px;
    width: min(500px,80vw);
    bottom:0;
    display: flex;
    justify-content: space-around;
}
.center{
   margin:auto;
}
.Footer a{
    transition: 0.15s;
    color:red;
    padding: 10px;
}
.Footer ul{
    margin:0;
    padding:0;
}
.Footer li{
    padding:0;
    margin:0;
}
.Footer a:hover{
    transform: scale(1.2);
}
a.social {
    margin: 0 1rem;
    transition: transform 100ms;
    display: inline-block;
  }
.fa-icon{
    color:red !important;
}
a.social:hover {
    transform: translateY(-5px);
  }
  @media (max-width:600px) {
      .Footer{
          width: 80%;
      }
      .Footer a{
        font-size: 15px;
        padding:0.05rem;
      }
      
  }
.Footer svg path{
    color:var(--secondary-nav);
    opacity: 0.9;
}
.Footer a:hover svg path{
    opacity: 0.8;
}