.Resume{
    background-color: var(--card-bg);
    box-shadow: rgba(0, 0, 0, 0.4) 0px 1px 4px;
    border-radius: 5px;
    padding: 20px;
    text-align: left;
    margin:auto;
    margin-top: 100px;
    color:var(--primary-text);
    width: min(90%,600px);
    justify-content:center;
}
.Resume ul{
color:var(--primary-text);
}
.Resume ul a{
    color:rgb(15, 99, 139);
}
.Resume li{
    padding: 5px;
    font-size: 16px;
    color:var(--primary-text)
}
.listHeader{
    font-size: 20px;
    font-weight: bold;
}
@media (max-width:600px) {
    .Resume{
        width: 95%;
        padding: 10px;
        overflow: wrap;
    }
    .Resume ul{
    width: 95%;
    }
    .Resume li{
        width: 95%;
    }
}
