.Home{
    padding:0;
    margin:0;
    height: 100%;
    width: 100vw;
   
}
html, body {
    height: 100%;
    width: 100%;
}

*{
    box-sizing: border-box;
}
body{
    padding:0;
    margin:0;
}


.Main{
    color:var(--primary-text);

margin: auto;
border-radius: 25px;


font-size: 25px;
}
.SText{
    font-size: 30px;
    margin-top: 200px;
    width: 50%;
    margin: auto;
    display:inline-block;
bottom:5%;
left:5%;
}
.About{
    color:var(--primary-text);
    font-size: 25px;
    padding: 20px;
    border-radius: 25px;
    width: 80%;
    margin: auto;
    margin-top: 100px;

}

.first-section{
    width:100vw;
    height:min(100vw,800px);
    position: relative;
    margin:0;
    background-color: rgba(15, 15, 15, 1);
    background-image: url('./bg2.jpg');
    display:flex;
    justify-content:center;
    align-items:flex-start;
    color:var(--secondary-text);
}
.Skills{
    display: flex;
    justify-content:center;
    background-color: var(--card-bg);
    box-shadow: rgba(0, 0, 0, 0.4) 0px 1px 4px;
    border-radius: 5px;
    padding: 20px;
    margin: 50px;
    color:var(--primary-text);
    margin-top: 100px;
    width: min(500px,80vw);
    display: inline-block;
    margin-bottom: 20px;
    opacity: 0.8;
    transition: 0.15s;
    box-sizing: border-box;
    position: relative;
    overflow: hidden;
}
.badge{
    width:15rem;
    height :3rem;
    background-color :#E65100;
    position: absolute;
    top:0;
    right:0;
    transform-origin:center;
    transform:rotateZ(45deg) translate(4rem,-2rem);
    overflow:hidden;
    text-align:center;
    font-size: 30px;
}
.Skills ul {
    text-decoration: none;
    text-align: center;
    font-size:20px;
    display:flex;
    flex-direction: column;
    align-items:center;
    justify-content:center;
}
.Skills li{
    font-weight : bold;
    background-color: rgb(0, 149, 230,0.5);
    border: rgb(0, 149, 230,0.5) solid 3px;
    border-radius: 50%;
    height:150px;
    width:150px;
    display:flex;
    color:var(--primary-text );
    justify-content:center;
    align-items: center;
    margin:10px;
    overflow-wrap: break-word;
}
.Skills span{
    
}
.openMenu{
  width: 35px;
  height: 5px;
  background-color: var(--secondary-nav);
  margin: 6px 0;
  transition: .15s;
}
.menu-bar-icon {
    color:var(--secondary-nav)
}
.menu-bar-icon :hover ,.menu-bar-icon:active{
    opacity : 0.8;
    background-color:whitesmoke;
    
}
.About{
    display: flex;
    justify-content:center;
    background-color: var(--card-bg);
    box-shadow: rgba(0, 0, 0, 0.4) 0px 1px 4px;

    border-radius: 5px;
    padding: 20px;
    margin: 50px;
    margin: auto;
    margin-top: 100px;
    width: min(500px,80vw);
    display: block;
    margin-bottom: 20px;
    opacity: 0.8;
    transition: 0.15s;
    box-sizing: border-box;
    position: relative;
    overflow: hidden;
    
    color:var(--primary-text );
}
.Typewriter__wrapper{
  color:var(--navtext-alt);  
}
.About ul{
    text-decoration: none;
    text-align: left;
    font-size:20px;
    
    justify-content:center;
}
.About li{
    font-weight: bold;
    color:var(--primary-text)
}
@media(max-width:600px)
{
 .SText{
    width: 100%;
    display: block;
 }   
 .first-section{
    flex-direction: column;
    justify-content:center;
    align-items:center;
 }
}
.header{
    font-weight: bold;
    color:var(--primary-text );
    
}
 html{
   background-color:var(--main-bg);
} 
