

.NavBar{
    font-size:22px;
    box-shadow: rgba(0, 0, 0, 0.3) 0px 1px 4px;
    background-color:var(--primary-nav);
    width: 100%;
    color:var(--navtext);
    padding: 20px;
    display: flex;
    align-items: center;
    justify-content:flex-end;
    position:relative;
}
.DarkModeIcon{
    font-size:20px;

}
.navbar-header{
    text-decoration: none;
    padding: 0.25em 1em;
    color:var(--navtext);
    vertical-align: middle;
    position: absolute;
    left:0;
    font-size:1.5rem;
    font-weight:bold;
}
.small_nav-header{
    font-size:1.5rem;
    font-weight:bold;
    vertical-align: middle;
    margin: auto;
}

.NavBar ul{
    text-decoration: none;
    display: inline;
    margin:0;
    padding:0;
    display: flex;
    justify-content:flex-end;
    align-items:center;
    text-align:center;
}
.NavbarElement{
    text-decoration: none;
    padding: 0.2em 1.5em 0.2em 1.5em;
    display: inline-block;
    color:var(--navtext);
    transition: 0.15s;

    vertical-align: middle;
    margin-left: 20px;
    
}

.NavbarElement:hover,.NavbarElement:active{
    color:var(--secondary-nav);
    transform: translateY(-5px);
   border-radius: 3px;


}
.NavbarElementAlt{
    text-decoration: none;
    padding: 0.2em 1.5em 0.2em 1.5em;
    display: inline-block;
    color:var(--navtext);
    transition: 0.15s;
    color:whitesmoke;
    border-radius: 0.25rem;
    
    vertical-align: middle;
    margin-left: 20px;
    background-color: inherit;
    border-radius: 0.25rem;
    border:solid var(--secondary-nav) 1px;
    color:var(--secondary-nav);
    
}

.NavbarElementAlt:hover,
.NavbarElementAlt:active{
    transform: translateY(-5px);
}

.NavBar button{
    border: none;
    color:var(--navtext);
    background-color: inherit;
    font-size: 25px;
}
.Active{
    color:var(--secondary-nav);
    border-radius: 3px;
}
.header-f{
    color:var(--navtext);
}
.header-l{
    color:var(--secondary-nav);
}
.btn-grp{
     position: absolute;
     left:5%;
}
.menu-button{
    color:var(--secondary-nav);
}
.OpenMenu{
    color:var(--secondary-nav);
}
.fa-icon{
   padding:0rem .4rem 0.2rem 0.4rem;
}
.NavBar svg path {
    color:var(--secondary-nav);
}